import { useCallback, useEffect, useState } from 'react';
import './trade.less'
import TradeInfo from './TradeInfo/TradeInfo'
import Tradingview from './Tradingview/Tradingview'
import { inject, observer } from 'mobx-react';
import MyInfo from './MyInfo/MyInfo';
import ContractInfo from './ContractInfo/ContractInfo';
import AccountInfo from './Account/AccountInfo';
import OrderBar from './Sidebar/OrderBar';
import classNames from 'classnames';
import SymbolInfo from './Sidebar/SymbolArea/SymbolInfo';
import { useLocation, useRouteMatch } from 'react-router';
import { useParams } from 'react-router-dom'
import Category from '../../model/Category';
import Type from '../../model/Type';
import TradeGuide from '../../components/TradeGuide/TradeGuide';
import TradeMaskHint from '../../components/TradeMaskHint/TradeMaskHint'
import { useWallet } from 'use-wallet'
import AnnouncementClose from '../../components/Announcement/AnnouncementClose';
import executor from '../../model/Executor';
import { getCookie } from '../../utils/utils';
import ZksyncRiskWarning from '../../components/RiskWarning/ZksyncRiskWarning';
import { show } from 'react-functional-modal';
import BorkerToBind from '../../components/BorkerToBind/BorkerToBind';
import useContract from '../../hooks/useContractLp';
import useLang from '../../hooks/useLang';
import { useModal } from 'react-hooks-use-modal';
import { keccak256, toUtf8Bytes } from 'ethers/lib/utils'
import useConfig from '../../hooks/useConfig';



function Trade({ trading, loading, wallet, version, lang, getLang, intl, type, category }) {
  const [loaded, setloaded] = useState(false)
  const [isMobileLayout, setIsMobileLayout] = useState(false)
  const isFutrueRetired = useRouteMatch({ path: '/retired/trade/futures' })
  const isOptionRetired = useRouteMatch({ path: '/retired/trade/options' })
  const isPowerRetired = useRouteMatch({ path: '/retired/trade/powers' })
  const location = useLocation();
  const walletContext = useWallet();
  const config = useConfig();
  const headerLang = useLang(intl.dict, 'header')
  const addressList = {
    56: "0x9A4f119904C6926EFE76F64BB70E35198a4dFF5f",
    42161: "0x772Fc767b6ff9841175922dE11DCaF9365091d2C"
  }
  const chainIdList = {
    56: 56,
    42161: 42161
  }
  const codeId = location.search && location.search.split('?')[2] && location.search.indexOf("?ref=") !== -1 ? location.search.split('?')[2].split("=")[1] : 42161
  const chainId = +codeId === +wallet.detail.chainId && chainIdList[wallet.detail.chainId] ? chainIdList[wallet.detail.chainId] : codeId
  const address = +codeId === +wallet.detail.chainId && addressList[wallet.detail.chainId] ? addressList[wallet.detail.chainId] : addressList[codeId]
  const RebateContract = useContract("RebateImplementation", chainId, address)
  const [BorkerToBindModal, openBorkerToBindModal, closeBorkerToBindModal] = useModal('root', {
    preventScroll: true,
    closeOnOverlayClick: false
  })
  const { symbol, baseToken } = useParams()

  const layoutClass = classNames('trade-layout', {
    'mobile-trade-layout': isMobileLayout,
    'gamma': trading.symbolInfo.isGamma
  })

  const orderBarClass = classNames('orderbar', {
    retired: (trading.pool && trading.pool.retired) || (trading.symbolInfo && trading.symbolInfo.retired)
  })

  //subscribe event for chainChanged and accountsChanged
  const chainChanged = (chainId) => {
    console.info('chainChanged. ', chainId)
    trading.pause();
  }
  const accountChanged = account => {
    if (wallet.account) {
      //executor
      executor.unsubscribe(wallet.account)
    }
    console.info('accountsChanged. ', account)
    trading.pause();
  }

  const getIsTrueCode = useCallback(async () => {
    let code = location.search.split('?')[1].split("=")[1]
    let keccak256Data = toUtf8Bytes(code)
    let keccak256Value = keccak256(keccak256Data)
    let res = await RebateContract.brokerAddresses(keccak256Value)
    if (wallet.detail.account) {
      let isBorker = await RebateContract.traderReferral(wallet.detail.account);
      if (isBorker !== "0x0000000000000000000000000000000000000000") {
        return false
      }
    }
    if (res !== "0x0000000000000000000000000000000000000000") {
      openBorkerToBindModal()
    }
  }, [RebateContract, location, wallet.detail.account])
  useEffect(() => {
    if (window.ethereum) {
      window.ethereum.on('chainChanged', chainChanged)
      window.ethereum.on('accountsChanged', accountChanged)
    }
    return () => {
      if (window.ethereum) {
        window.ethereum.removeListener('chainChanged', chainChanged)
        window.ethereum.removeListener('accountsChanged', accountChanged)
      }
    }
  }, [window.ethereum])

  useEffect(() => {
    if (type) {
      Type.setCurrent(type)
    }
    if (category) {
      Category.setCurrent(category)
    }
  }, [type, category]);

  useEffect(() => {
    let ZkSyncCookie = getCookie("ZksyncRiskWarning-modal")
    if (ZkSyncCookie !== "false" && +wallet.detail.chainId === 324) {
      show(<ZksyncRiskWarning />, {
        key: "ZksyncRiskWarning",
        fading: true,
        style: {
          background: "rgba(0, 0, 0, 0.4)",
          zIndex: 9,
        },
      })
    }
  }, [wallet.detail.chainId])

  useEffect(() => {
    if (RebateContract && location.search.indexOf("?ref=") !== -1) {
      wallet.switchNetwork(config[codeId])
      getIsTrueCode()
    }

  }, [RebateContract, location])

  useEffect(() => {
    loading.loading();
    if (walletContext.status !== 'connecting') {
      wallet.setStatus(walletContext.status)
      trading.init(wallet, () => {
        setloaded(true)
      }, isFutrueRetired || isOptionRetired || isPowerRetired, symbol, baseToken)
    }

    return () => trading.clean()
  }, [wallet, wallet.detail.chainId, version.current, Type.current, Category.current, symbol, baseToken, walletContext.status])
  return (
    <div>
      <TradeGuide lang={lang} trading={trading} wallet={wallet} getLang={getLang} />
      <AnnouncementClose />
      <div className={layoutClass}>
        <div className='symbol-area'><SymbolInfo lang={lang} onGirdLayoutChange={(isMobileLayout) => setIsMobileLayout(isMobileLayout)} /></div>
        <div className='account-info'>
          <TradeMaskHint postion="accountInfo" />
          <AccountInfo lang={lang} getLang={getLang} />
        </div>
        <div className={orderBarClass}><OrderBar lang={lang} /></div>
        <div className='trade-info'><TradeInfo lang={lang} getLangKey={getLang} /></div>
        <div className='kline'><Tradingview lang={lang} /></div>
        <div className='my-info'><MyInfo lang={lang} getLang={getLang} /></div>
        <div className='contract'><ContractInfo lang={lang} getLang={getLang} /></div>
      </div>
      <BorkerToBindModal>
        <BorkerToBind contract={RebateContract} lang={headerLang} wallet={wallet} closeBorkerToBindModal={closeBorkerToBindModal} />
      </BorkerToBindModal>
    </div>

  )
}

export default inject('wallet', 'type', 'version', 'loading', 'trading', 'intl')(observer(Trade))
