import Icon from '../Icon/Icon'
import './borkerToBind.less'
import { EVENT_TRANS_BEGIN, EVENT_TRANS_END, SUPPORTED_WALLETS } from '../../utils/Constants'
import { useCallback, useEffect, useMemo, useState } from 'react'
import { formatAddress } from '../../utils/utils'
import Button from '../Button/Button'
import Emitter from '../../utils/Emitter'
import { useModal } from 'react-hooks-use-modal';
import WalletView from '../Header/WalletView'
import WalletDialog from '../WalletModal/WalletModal'
import { useLocation } from 'react-router'
import { useWallet } from 'use-wallet'
export default function BorkerToBind({ wallet, contract, lang, closeBorkerToBindModal }) {
  const [disabled, setDisabled] = useState(true)
  const location = useLocation();
  const walletContext = useWallet();
  const brokerCode = location.search.split('=')[1].split('?')[0]
  const codeId = location.search.split('?')[2].split("=")[1]
  const [WalletModal, openWalletModal, closeWalletModal] = useModal('root', {
    preventScroll: true,
    closeOnOverlayClick: false
  });
  const transactionTitle = {
    text: "Confirm Your Broker",
    icon: ""
  }
  const transactionContent = {
    success: "Confirm Your Broker",
    error: "Transaction Failed"
  }
  const curConnector = useMemo(() => {
    const connector = localStorage.getItem("connector")
    if (connector) {
      return JSON.parse(connector)
    }
    return SUPPORTED_WALLETS.injected
  })
  const click = useCallback(async () => {
    if (contract) {
      let res
      try {
        res = await contract.registerTrader(brokerCode)
      } catch (error) {
        return false;
      }
      if (res && res.hash) {
        Emitter.emit(EVENT_TRANS_BEGIN, {
          title: transactionTitle, content: transactionContent, hash: res.hash
        })
      } else {
        return false
      }
      const receipt = await res.wait()
      console.log("receipt", receipt)
      if (receipt) {
        console.log("receipt", receipt)
        if (receipt && receipt.transactionHash) {
          const hash = receipt.transactionHash
          if (receipt.status) {
            closeBorkerToBindModal()
            Emitter.emit(EVENT_TRANS_END, { title: transactionTitle, content: transactionContent, context: { success: true, hash: hash, error: "error" } })
          } else {
            Emitter.emit(EVENT_TRANS_END, { title: transactionTitle, content: transactionContent, context: { success: false.success, hash: hash, error: "error" } })
          }
        }
      }
    }
  }, [contract])

  useEffect(() => {
    if (+wallet.detail.chainId === +codeId && wallet.isConnected()) {
      setDisabled(false)
    } else {
      setDisabled(true)
    }
  }, [wallet,wallet.detail.chainId])
  return <div className="borker-bind-trade">
    <div className='borker-bind-modal-header'>
      Confirm Referral
      <Icon token="close-modal-icon" onClick={() => closeBorkerToBindModal()}  />
    </div>
    <div className='borker-bind-modal-info'>
      <div className='borker-bind-modal-des'>
         You have been invited to participate in DERI brokerage program
      </div>
      <div className='wallet-borker-account'>
        <Icon token={curConnector.icon} width='20' />
        <div className='connected-with'>
          {/* <span>
            Connected With
          </span> */}
          <span className='text-account'>
            {wallet.detail.account ? wallet.detail.account : "--"}
          </span>
        </div>
      </div>
      <div className='your-borker-code'>
        
        <div className='refrral-code'>
          <span>
            Referral Code
          </span>
          <span className='text-code'>
            {brokerCode}
          </span>
        </div>
      </div>
      <div className='confirm-btn'>
        {wallet.isConnected() ? <Button width="100%" disabled={disabled} onClick={click} label="CONFIRM" /> :
          <Button width="100%" label="Connect Wallet" onClick={openWalletModal} />}
      </div>
    </div>
    <WalletModal>
      <WalletDialog walletContext={walletContext} closeModal={closeWalletModal} lang={lang} wallet={wallet} isWrongNetwork={false} />
    </WalletModal>
  </div>
}